<template>
  <div>
    <div class="page-content home">
      <div class="swipe-wrapper">
        <van-swipe class="swipe" :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(item,index) in 4" :key="index">
            <van-image
              width="100%"
              height="100%"
              fit="contain"
              lazy-load
              :src="banner"
            />
          </van-swipe-item>
        </van-swipe>
        <div class="swipe-edit" @click="editSwiper">
          <i class="iconfont iconedit" />
        </div>
      </div>
      <div class="section">
        <van-row gutter="7" class="goods">
          <van-col span="12" v-for="item in 6" :key="item">
            <div class="good">
              <van-image
                class="good-img"
                fit="contain"
                lazy-load
                :src="good"
              />
              <div class="good-dec">
                蜜炼川贝枇杷膏
              </div>
              <div class="good-aside">
                <div class="good-aside-price">
                  ￥<span>36.0</span>
                </div>
                <div class="good-aside-edit" @click="editGoods">
                  <i class="iconfont iconedit"/>
                </div>
              </div>
            </div>
          </van-col>
        </van-row>
      </div>
    </div>
    <Tabbar/>
    <ActivityIndicator :visible="$loadingPlugin.getList"/>
  </div>
</template>

<script>
import { Swipe, SwipeItem, Image, Row, Col } from 'vant';
import Tabbar from '@/components/Tabbar'
import ActivityIndicator from '@/components/ActivityIndicator'
import timeout from '@/utils/timeout.js'

const banner = require('../../assets/banner.png');
const good = require('../../assets/good.png');

export default {
  name: 'shop',
  components: {
    [Swipe.name]:Swipe,
    [SwipeItem.name]:SwipeItem,
    [Image.name]:Image,
    [Row.name]: Row,
    [Col.name]: Col,
    Tabbar,
    ActivityIndicator
  },
  data() {
    return {
      banner,
      good
    }
  },
  async created(){
    await this.getList();
  },
  methods: {
    editSwiper() {
      this.$router.push('/swiper')
    },
    editGoods() {
      this.$router.push('/push')
    },
  },
  asyncMethods: {
    async getList(){
      await timeout(3000)
    }
  }
};
</script>

<style lang="less" scoped>
  .home{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 100/@rem;
    overflow-y: scroll;
    background: #fff;
    padding-bottom: 20/@rem;
  }
  .swipe{
    height: 358/@rem;
  }
  .swipe-wrapper{
    position: relative;
    .swipe-edit{
      position: absolute;
      z-index: 10;
      bottom: 16/@rem;
      right: 46/@rem;
      height: 58/@rem;
      width: 58/@rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(0,0,0,0.30);
      border-radius: 50%;
      i{
        color: #fff;
        font-size: 32/@rem;
        margin-left: 4/@rem;
      }
    }
  }
  .goods{
    margin: 30/@rem 20/@rem 0!important;
  }
  .good{
    background: #FFFFFF;
    box-shadow: 0 0 10/@rem 0 rgba(109,155,192,0.36);
    border-radius: 12/@rem;
    margin-top: 14/@rem;
    padding: 16/@rem;
    &-img{
      width: 100%;
      height: 228/@rem;
      background: rgba(255,245,235,0.50);
    }
    &-dec{
      font-size: 30/@rem;
      color: #242424;
    }
    &-aside{
      height: 60/@rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      &-price{
        color: @red;
        font-size: 22/@rem;
        font-weight: bold;
        span{
          font-size: 34/@rem;
        }
      }
      &-edit{
        height: 58/@rem;
        width: 58/@rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background-image: linear-gradient(-220deg, #FFCB5B 2%, #FE9C2E 100%);
        border-radius: 50%;
        box-shadow: 0 0 10px 0 rgba(255,203,91,0.66);
        i{
          color: #fff;
          font-size: 32/@rem;
          margin-left: 3px;
        }
      }
    }
  }
</style>

<style lang="less">
  .home{
    .van-swipe__indicators{
      bottom: 12/@rem;
      .van-swipe__indicator{
        width: 8px;
        height: 8px;
        box-sizing: border-box;
        border: 1px solid #fff;
        background: transparent;
        opacity: 1;
      }
    }
  }
</style>